@import "mixin.scss";
$tertiary:#007bff;
$secondary:#d5d5d5;

body{
padding:0px;
margin: 0px;
background: #d5d5d5;
}
/* Style the header: fixed position (always stay at the top) */
.header {
  position: fixed;
  top: 0;
  z-index: 1;
  width: 100%;
  background-color: #f1f1f1;
}

/* The progress container (grey background) */
.progress-container {
  width: 100%;
  height: 8px;
  background: #343a40;
}

/* The progress bar (scroll indicator) */
.progress-bar {
  height: 8px;
  background: #399fd2;
  width: 0%;
}


/*Universal css starts*/
.navbar {
  font-size: 20px;
  margin-bottom: 0px;
}
.navbar img {
  border-radius: 50%;
}
.nav-item {
  margin-right: 10px;
}
.brand-styling {
  text-transform: uppercase;
  font-size: 1.1em;
}
.brand-styling .nav-item {
  margin-right: 35px;
}
footer {
  background-color: #343a40;
  height: 50px;
  text-align: center;
  color: #FFF;
}

.navbar-dark{
     .navbar-nav
     .nav-link {
     color:#fff;
     transition: .3s;
     }
}
.navbar-dark .navbar-nav:hover .nav-link
{
opacity: .2;
filter: blur(15px);
}
nav .navbar-nav .nav-item:hover  .background
{
opacity: .2;
filter: blur(5px);
}
.navbar-dark .navbar-nav .nav-link:hover{
 box-shadow: 3px 3px 12px rgba(0,0,0,.5);
border-radius: 5px;
  background-color: #343a40;
  opacity: 1;
filter: blur(0px);
color:#fff;
}
/*Universal css ends*/

.jumbotron{
background:url("ourwork.jpg");
background-size:cover;
margin-bottom:8px;
text-align: center;
height:100vh;
 img{
border-radius: 50%;
height:15%;
width:25%;
}
 h1{
color:#fff;
}
}
.landing-box{
margin:-25px 0 0 0;
background:#fff;
height:1000px;
padding-top:15px;
}
.landing-box img{
height: 232px;
width: 358px;
padding-bottom: 30px;
line-height: 2px;
margin-bottom: 55px;
border-bottom:2px solid #d5d5d5;
}

.landing-box {
>.col-lg-12{
text-align: center;
color: crimson;
text-transform: uppercase;
text-shadow: 0 0 15px crimson;
font-family: 'Playfair Display', serif;
margin-bottom:-20%;
}
h3{
text-align: center;
}
}
.landing-box .line{
background: crimson;
height: 2px;
width: 100%;
margin-bottom:0px;
}

.media-background{
padding: 15px;
height:310px;
width:380px;
background:#fff;
margin-bottom: 10px;
}
.media-background:hover{
box-shadow: 0px 0px 15px 5px rgba(0,0,0,.5);
transition: box-shadow 1s;
}
.col-lg-4{
  overflow: hidden;
}
.media-description{
position:absolute;
width: 360px;
top:500px;
height:232px;
left:29px;
background:rgba(213,213,213,.4);
}
.landing-box .media-object:hover .media-description{
top:29px;
cursor: pointer;
-webkit-transition: all .5s;
}

.media-object{
padding:15px;
}
.media-description .btn{
margin-top:150px;
margin-left:116px;
background-color:$secondary;
border: 2px solid $secondary;
color:#000;
font-weight:500;
}
.media-heading{
position:absolute;
font-family: 'Montserrat', sans-serif;
top:270px;
color:#000;
}
.media-object img{
width:320px;
}

.briefing{
padding: 15px;
height: 100%;
margin-left:0;
margin-right:0;
width:100%;
overflow: hidden;
font-size:183%;
border-bottom:2px solid red;

.briefing-content{
position: relative;
float: left;

p{
color:$tertiary;
font-weight:600;
font-family: 'Indie Flower', cursive;
}
}
.briefing-media{
position:relative;

img{
  float:right;
}
a{
  position: absolute;
  top: 102%;
  left: 62%;
  text-align: center;
 cursor:pointer;
}
.btn:hover{
  cursor: pointer;
}
.btn{
width:358px;
font-size: 25px;
background-color:$tertiary;
clip-path:polygon(0 0,100% 0,100% 100%,25% 100%);
border: 2px solid $secondary;
color:#fff;
}
}
}
.briefing:hover .briefing-media a{
  //-webkit-transform:translateX(100%);
  //transform:translateX(100%);
  //transition: all .5s;
}
.briefing-header{
margin-top: 15  px;
height: 80px;
margin-bottom:15px;
height: 120px;
h1{
font-family: 'Dancing Script', cursive;
color:$tertiary;
font-size:35px;
text-align: center;

}
h1:hover{
cursor: pointer;
}
}
i{
transform: scale(3);
margin-left:20px;
}
div:nth-child(6){
animation-delay:.2s;
  }
div:nth-child(7){
 animation-delay:.4s;
}
.technologies{
position: relative;
overflow: visible;
text-align: center;
font-size:25px;
padding-top: 19px;
color:$tertiary;
bottom:15px;
animation: jump 1.5s infinite ease-in-out;
margin-bottom: 50px;
}
.heading{
  text-align: center;
  font-size:25px;
  padding-top: 19px;
  color:$tertiary ;
  margin-bottom:36px;
  margin-top:124px;
}
@keyframes jump{
  0%{
  bottom: 0;

  }
  50%{
    bottom:27px;
  }
  100%{
    bottom:0;
}
  }
