//Media-queries
@media (max-width:600px)
{
.landing-box{
  display: none;
}
#YANTRIX,#ECELL,#MUN,#MLA_TRACK,#AEMWIA{
background:none;

}
}
@media (max-width:1000px)
{
  body{
 .briefing .technologies{
  animation: none;
  }
 .landing-box{
    display: none;
}
}
}
